.crossfade {
  position: absolute;
  width: 100%;
  height: 100%;
  background: black;
}

.crossfade > figure {
  animation: imageAnimation 60s linear infinite 0s;
  backface-visibility: hidden;
  background-size: cover;
  background-position: center;
  position: absolute;
  width: 100%;
  height: 100%;
  color: transparent;
  opacity: 0;
  position: absolute;
  margin: 0;
}

.crossfade > figure:nth-child(1) { 
  animation-delay: 0s;
  background-image: url('/3dena.jpeg'); }

.crossfade > figure:nth-child(2) {
  animation-delay: 12s;
  background-image: url('/mulin.jpeg');
}

.crossfade > figure:nth-child(3) {
  animation-delay: 24s;
  background-image: url('/prova.jpeg');
}

.crossfade > figure:nth-child(4) {
  animation-delay: 36s;
  background-image: url('/parco.jpeg');
}

.crossfade > figure:nth-child(5) {
  animation-delay: 48s;
  background-image: url('/mulin.jpeg');
}



@keyframes 

imageAnimation {  0% {
 animation-timing-function: ease-in;
 opacity: 0;
}
 8% {
 animation-timing-function: ease-out;
 opacity: 1;
}
 17% {
 opacity: 1
}
 25% {
 opacity: 0
}
 100% {
 opacity: 0
}
}


