.grid-container {
  display: grid;
  justify-content: space-around;
  grid-template-columns: auto auto auto; /*Make the grid smaller than the container*/
  grid-column-gap: 25px;
  grid-row-gap: 1em;
  padding: 10px;
}

.grid-container > div {
  background-color: #FFFFFF;
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
  margin: auto;
}

.firstColumn{
  grid-column: 1 / span 2;
  grid-row: 2;
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px 0;
  font-size: 20px;
}

.lastColumn{
  grid-column: 3;
    grid-row: 2;
    background-color: rgba(255, 255, 255, 0.8);
    padding: 20px 0;
    font-size: 20px;
    margin-right: 35px;
  margin: auto;

}

