.grid-1 {
  display: grid;
  width: 100%;
  margin: 0 auto;
  
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap: 10px;
  
  grid-template-areas: "uno"
                       "due"
                       "tre"
                       "cinque"
                       "quattro"
                       "sei"
                       "sette"
                       "nove"
                       "otto";
}

/* items */

.grid-1 div {
  color: white;
  font-size: 16px;
  padding: 10px;
  }

/* rwd */

@media only screen and (min-width: 600px) {

  .grid-1 {
    max-width: 1800px;
    
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    grid-gap: 20px;
    
    grid-template-areas:  "uno uno uno"
                          "due due tre"
                          "quattro cinque cinque"
                          "sei sei sette"
                          "otto nove nove";
  }

  .grid-1 div {
    font-size: 20px;
    padding: 20px;
   }

