* {
  box-sizing: border-box;
}

.columnGallery {
  float: left;
  width: 33%;
  padding: 15px;
  height: 370px;
}

/* Clearfix (clear floats) */
.rowGallery::after {
  content: "";
  clear: both;
  display: table;

}

@media screen and (max-width: 1025px) {
  .columnGallery {
    width: 50%;
    height: 370px;
  }
}

/* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
@media screen and (max-width: 550px) {
  .columnGallery {
    width: 100%;
    height: auto;
  }
}


.cardGall {
   position: relative;
   width: 100%;
    height: 100%;
    cursor: pointer;
}

.overlay {
   position: absolute;
   display: flex;
  justify-content: center;
  align-items: center;
   bottom: 0;
   background: rgba(0, 0, 0, 0.4);
   width: 100%;
    height: 100%;
   transition: .5s ease;
   opacity:0;
   color: rgb(255, 255, 255);
   font-size: 22px;
   font-weight: 300;
   padding: 30px;
   text-align: center;
}
.cardGall:hover .overlay {
   opacity: 1;
}



